
export default {
	globalData:{
		statusBar:0,
		VerificationCode(json){//封装一个方法来获取验证码
			console.log(json)
			uni.request({
				url:"http://hbk.huiboke.com/api/common/getSMSCaptcha",
				method:"POST",
				data:json,
				success(res){//成功的回调
					console.log(res)
				}
			})
		},

			userbinding(bingjson){//2将用户微信账户与app平台账户绑定
				uni.request({
					url:"http://hbk.huiboke.com/api/login_and_register/bindUser",
					method:"POST",
					data:bingjson,
					success(res){
						console.log(res)
					}
				})
			}

	},
	onLaunch: function() {
		const _this = this
		uni.getSystemInfo({
			success(e){
				_this.globalData.statusBar = e.statusBarHeight
				// console.log(e.statusBarHeight)
			}
		})
		//提示用户平台有多次登录让用户认真操作每一步
		uni.showToast({
			title:"平台为确保用户的信息安全,进行了多型登录",
			icon:"none",
			duration:3000
		})
	},
	onShow: function() {
		
	},
	onHide: function() {
		
	},
	methods:{
		
	}
}
